<template>
  <el-dialog
    v-model="visible"
    title="修改密码"
    width="500px"
    :close-on-click-modal="false"
    :show-close="false"
    :close-on-press-escape="false"
    destroy-on-close
  >
    <el-alert
      title="首次登录需要修改密码"
      type="warning"
      :closable="false"
      show-icon
      style="margin-bottom: 20px"
    />
    
    <el-form
      ref="formRef"
      :model="form"
      :rules="rules"
      label-width="120px"
      @submit.prevent
    >
      <el-form-item label="新密码" prop="new_password">
        <el-input
          v-model="form.new_password"
          type="password"
          placeholder="请输入新密码"
          show-password
          clearable
          @keyup.enter="handleSubmit"
        />
      </el-form-item>
      
      <el-form-item label="确认密码" prop="confirm_password">
        <el-input
          v-model="form.confirm_password"
          type="password"
          placeholder="请再次输入新密码"
          show-password
          clearable
          @keyup.enter="handleSubmit"
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleSubmit" type="primary" :loading="loading">
          确认修改
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script setup lang="ts">
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import request from '@/utils/request'

const emit = defineEmits<{
  (event: 'success'): void
  (event: 'close'): void
}>()

const visible = ref(false)
const loading = ref(false)
const adminId = ref(0)

const form = reactive({
  new_password: '',
  confirm_password: ''
})

const formRef = ref()

const rules = {
  new_password: [
    { required: true, message: '请输入新密码', trigger: 'blur' },
    { min: 8, message: '密码长度至少8位', trigger: 'blur' },
    { 
      validator: (rule: any, value: string, callback: any) => {
        if (value && value.length < 8) {
          callback(new Error('密码长度至少8位'))
        } else if (value && !/(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[^a-zA-Z\d])/.test(value)) {
          callback(new Error('密码必须包含大小写字母、数字、特殊字符'))
        } else {
          callback()
        }
      }, 
      trigger: 'blur' 
    }
  ],
  confirm_password: [
    { required: true, message: '请确认密码', trigger: 'blur' },
    {
      validator: (rule: any, value: string, callback: any) => {
        if (value !== form.new_password) {
          callback(new Error('两次输入的密码不一致'))
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ]
}

const open = (userId: number) => {
  visible.value = true
  adminId.value = userId
  // 清空表单
  form.new_password = ''
  form.confirm_password = ''
}

const handleSubmit = async () => {
  try {
    await formRef.value?.validate()
    loading.value = true
    
    const response = await request.post('/api/v1/admin/change-password', {
      admin_id: adminId.value,
      new_password: form.new_password,
      confirm_password: form.confirm_password
    })
    
    console.log('密码修改API响应:', response)
    
    if (response.code === 200) {
      ElMessage.success('密码修改成功，请重新登录')
      visible.value = false
      emit('success')
    }
    // 错误消息由拦截器统一处理
  } catch (error: any) {
    console.error('密码修改失败:', error)
    console.error('错误详情:', error.response?.data)
    // 错误消息由拦截器统一处理，这里不再重复显示
  } finally {
    loading.value = false
  }
}

defineExpose({
  open
})
</script>

<style scoped>
.dialog-footer {
  text-align: right;
}
</style>
